import mockApis from './mock'
import loader from '@/loader/loader'
import User from './scripts/user.js'
import Top10 from './scripts/top10'
import InNetwork from './scripts/InNetwork.js'
import PhoneNumber from './scripts/phoneNumber.js'
import ControlTaskCout from './scripts/ControlTaskCout.js'
import './index.css'

const user = new User()
const inNetwork = new InNetwork()
const phoneNumber = new PhoneNumber()
const top = new Top10()
const controlTaskCout = new ControlTaskCout()

loader.load({
  apis: mockApis, 
  config: {}, 
  init() {
    // 用户信息
    user.render()
    // 全市在网终端、在网人数
    inNetwork.render()
    // 在渝、出渝手机数量统计
    phoneNumber.render()
    // 渲染管控任务数量统计
    controlTaskCout.render()
    // top排行
    top.init()
  }
})



