
import $ from 'jquery'
import { fetch } from '@/util/request'
import top10Hbs from '../templates/top10.hbs'
import topList from '../templates/topList.hbs'
import topDetialsHbs from '../templates/topDeitals.hbs'

const setTime = 5000
let type = ''
let state = ''

export default class Top10 {
  constructor() {
    // 存储请求回来的数据，一次请求回来的数据包括省份、地市级和国外三种数据
    this.inTopListData = {}
    this.outTopListData = {}
    // 渲染Top10模板
    $('.in-cq-top').html(top10Hbs())
    $('.out-cq-top').html(top10Hbs())
    // 初始化时候修改出渝Top10标题
    $('.out-cq-top .yu-type').text('出渝')
    $('.top-detials-dialog').html(topDetialsHbs())
  }
 
  /**
   *  渲染top10列表
   *  @param    {string}  selector 容器元素选择
   *  @param    {array}  data 数据
   *  @return   {object}  null
   */
  renderTopList(selector, data) {
    data.length = data.length
    $(`${selector} .top10-list`).html(topList(data))
  }

  /**
   *  发送ajax请求，获取在渝数据
   *  @return   {object}  null
   */
  fetchInData() {
    const self = this
    fetch('fetchInYuTop',
      data => {
        self.inTopListData = data.inYuTop10
        self.renderTopList('.in-cq-top', self.inTopListData.province)
      }
    )
  }
 
  /**
   *  发送ajax请求，获取出渝数据
   *  @return   {object}  null
   */
  fetchOutData() {
    const self = this
    fetch('fetchOutYuTop',
      data => {
        self.outTopListData = data.outYuTop10
        self.renderTopList('.out-cq-top', self.outTopListData.province)
      }
    )
  }
 
  /**
   *  绑定事件
   *  @return   {object}  null
   */
  bindEvent() {
    const self = this
    // 在渝手机top10点击切换
    $('.in-cq-top').on('click', '.nav', (evt) => {
      const $this = $(evt.target)
      self.switchNav('.in-cq-top', $this, '在渝')
    })

    // 出渝手机top10点击切换
    $('.out-cq-top').on('click', '.nav', (evt) => {
      const $this = $(evt.target)
      self.switchNav('.out-cq-top', $this, '出渝')
    })

    // 鼠标移上去
    $('.top10-list').on('mouseover', 'li a:nth-child(1)', (evt) => {
      self.showAllName(evt)
    })

    // 鼠标移动
    $('.top10-list').on('mousemove', 'li a:nth-child(1)', (evt) => {
      self.showAllName(evt)
    })

    // 鼠标离开
    $('.top10-list').on('mouseout', 'li a:nth-child(1)', () => {
      $('.show-all-name').hide().text('')
    })

    // 渲染详情
    self.renderDetails()

    // 点击搜索
    $('.search-btn').on('click', () => {
      $('.search-list').scrollTop(0)
      self.renderSearch()
    })

    // 获取input框的keyCode
    $('.search-text').on('keypress', (evt) => {
      let keyCode = evt.keyCode
      // 回车
      if(keyCode === 13){
        $('.search-list').scrollTop(0)
        self.renderSearch()
      }
    })

  }

  /**
   *  渲染搜索
   *  @return   {object}  null
   */
  renderSearch() {
    let text = $('.search-text').val()
    if(text === '') {
      return $('.search-text').attr('placeholder', '关键字不能为空')
    }
    $('.search-text').attr('placeholder', text)
    $('.search-text').val('')
    fetch('fetchSearch', {state, type, text}, (data) => {
      $('.search-list').html(topList(data.searchList))
    })
  }

  /**
   *  显示全部地区名字
   *  @param    {object}  evt 鼠标事件
   *  @return   {object}  null
   */
  showAllName(evt) {
    const $this = $(evt.target)
    let name = $.trim($this.text())
    if(name.length >= 5) {
      $this.css('cursor', 'pointer')
      let left = evt.clientX 
      let top = evt.clientY + 20
      $('.show-all-name').css({'left': left, 'top': top}).show().text(name)
    }
  }

  /**
   *  渲染详情
   *  @return   {object}  null
   */
  renderDetails() {
    const self = this
    // 在渝手机top10点击查看详情
    $('.in-cq-top').on('click', '.top-details', () => {
      // 获取当前选择项的类型(province, city, abroad)
      let selector = $('.in-cq-top').find('.active')
      type = selector.attr('type')
      let typezn = selector.attr('type-zn') 
      console.log()
      $('.detials-content .current-type').text(typezn)
      $('.detials-content .yu-type').text('在渝')
      state = 'in'
      // 发送请求
      fetch('fetchInYuTopDetails', {type}, (data) => {
        if(typezn === '省级') {
          typezn = '省级名称'
        }
        $('.search-text').val('').attr('placeholder', `请输入${typezn}进行排行搜索`)
        $('.top-detials-dialog').show().find('.search-list').html(topList(data.inYuTopDetails))
      })
    })

    // 出渝手机top10点击查看详情
    $('.out-cq-top').on('click', '.top-details', () => {
      // 获取当前选择项的类型(province, city, abroad)
      let selector = $('.out-cq-top').find('.active')
      type = selector.attr('type')
      let typezn = selector.attr('type-zn') 
      $('.detials-content .current-type').text(typezn)
      $('.detials-content .yu-type').text('出渝')
      state = 'out'
      fetch('fetchOutYuTopDetails', {type}, (data) => {
        if(typezn === '省级') {
          typezn = '省级名称'
        }
        $('.search-text').val('').attr('placeholder', `请输入${typezn}进行排行搜索`)
        $('.top-detials-dialog').show().find('.search-list').html(topList(data.outYuTopDetails))
      })
    })

    // 鼠标移上去
    $('.search-list').on('mouseover', 'li a:nth-child(1)', (evt) => {
      self.showAllName(evt)
    })

    // 鼠标移动
    $('.search-list').on('mousemove', 'li a:nth-child(1)', (evt) => {
      self.showAllName(evt)
    })

    // 鼠标离开
    $('.search-list').on('mouseout', 'li a:nth-child(1)', () => {
      $('.show-all-name').hide().text('')
    })

    // 点击关闭
    $('.top-detials-dialog').on('click', '.close', () => {
      $('.top-detials-dialog').hide()
    })
  }

  /**
   *  切换导航
   *  @param    {string}  selector   元素选择器
   *  @param    {object}  source 来源事件
   *  @param    {string}  text   文字(在渝、出渝)
   *  @return   {object}  null
   */
  switchNav(selector, source, text) {
    const self = this
    const $this = source
    const current = $this.attr('type')
    const currentZN = $this.attr('type-zn')
    $this.siblings().removeClass('active')
    $this.addClass('active')
    $(`${selector} .yu-type`).text(text)
    $(`${selector} .current-type`).text(currentZN)
    if(text === '在渝') {
      self.renderTopList(selector, self.inTopListData[current])
    }else {
      self.renderTopList(selector, self.outTopListData[current])
    }
  }

  /**
   *  自动切换数据(功能暂保留，未调用)
   *  @param    {[type]}  i [description]
   *  @return   {object}  null
   */
  autoSwitchData(i) {
    const self = this
    let num = i
    let num2 = i
    // 在渝定时切换
    clearInterval(self.inSetInterval)
    self.inSetInterval = setInterval(() => {
      const $inThis = $('.in-cq-top .nav').eq(num)
      self.switchNav('.in-cq-top', $inThis, '在渝')
      num++
      num === 3 ? num = 0 : ''
    }, setTime)

    // 出渝定时切换
    clearInterval(self.outSetInterval)
    self.outSetInterval = setInterval(() => {
      const $outThis = $('.out-cq-top .nav').eq(num2)
      self.switchNav('.out-cq-top', $outThis, '出渝')
      num2++
      num2 === 3 ? num2 = 0 : ''
    }, setTime)
  }
 
  /**
   *  top list初始化方法
   *  @return   {object}  null
   */
  init() {
    const self = this
    self.fetchInData()
    self.fetchOutData()
    self.bindEvent()
  }
}



