export default {
  url: '/main/outYuTopDetails/:type',
  mock: {
    'code': 1,
    'msg': 'success',
    'result': {
      'outYuTopDetails|20-150': [
        {
          'name': '@province',
          'value|1-3000': 1,
          'increase|1-100': 1,
          'flag|1': [0, 1, -1]
        }
      ]
    }
  }
}



