import $ from 'jquery'
import { fetchInterval } from '@/util/request'
import hbs from '../templates/controlTaskCout.hbs'
import loader from '@/loader/base.config.js'
// 数字滚动
import DigitRoll from '@/util/digitroll.js'

export default class Bottom {

  constructor() {
    this.interval = null
  }
  /**
   *  渲染各模块组件
   *  @return   {object}  null
   */
  render() {
    // 10分钟更新数据 请求管控任务数量统计数据
    fetchInterval('fetchControlTaskCout', (data) => {
      $('.pages-bottom').html(hbs(data.controlTaskCout))
      this.digitRoll(data.controlTaskCout)
    }, loader.updateTime)
  }
  /**
   *  数字滚动
   *  @param    {object}  data 数据
   *  @return   {object}  null
   */
  digitRoll(data) {
    // 任务数量
    let totalPerson = data.totalPerson
    let totalPersonRoll = new DigitRoll({
      container:'.total-person .num-roll'
    })
    totalPersonRoll.roll(totalPerson)
    // 预警次数
    let warning = data.warningNumber
    let warningRoll = new DigitRoll({
      container:'.warning-number .num-roll'
    })
    warningRoll.roll(warning)
    // 清除定时切换
    clearInterval(this.interval)
    // 定时切换
    this.interval = setInterval(function () {
      // 任务数量
      totalPerson += parseInt(Math.random() * 10, 10)
      totalPersonRoll.roll(totalPerson)
      // 预警次数
      warning += parseInt(Math.random() * 10, 10)
      warningRoll.roll(warning)
    }, loader.aniTime) 
  }

}



