
export default {
  url: '/main/outYuTop10',
  mock: {
    'code': 1,
    'msg': 'success',
    'result': {
      'outYuTop10': {
        'province|10': [
          {
            'name': '@province',
            'value|1-9999999': 1,
            'increase|1-100': 1,
            'flag|1': [1, -1, 0]
          }
        ],
        'city|0': [
          {
            'name': '@city',
            'value|1-9999999': 1,
            'increase|1-100': 1,
            'flag|1': [1, -1, 0]
          }
        ],
        'abroad|10': [
          {
            'name': '@pick(["俄罗斯", "美国", "墨西哥", "菲律宾", "巴西"])',
            'value|1-9999999': 1,
            'increase|1-100': 1,
            'flag|1': [1, -1, 0]
          }
        ]
      }
    } 
  }
}


