/**
 * @Author:      zhanghq
 * @DateTime:    2017-10-18 11:19:56
 * @Description: 在渝手机排行TOP10数据
 * @Last Modified By:   zhanghq
 * @Last Modified Time:    2017-10-18 11:19:56
 */

export default {
  url: '/main/inYuTop10',
  mock: {
    'code': 1,
    'msg': 'success',
    'result': {
      'inYuTop10': {
        'province|10': [
          {
            'name': '@province',
            'value|1-3000': 1,
            'increase|1-100': 1,
            'flag|1': [0, 1, -1]
          }
        ],
        'city|10': [
          {
            'name': '@city',
            'value|1-3000': 1,
            'increase|1-100': 1,
            'flag|1': [0, 1, -1]
          }
        ],
        'abroad|10': [
          {
            'name': '@pick(["俄罗斯", "美国", "墨西哥", "菲律宾", "巴西"])',
            'value|1-3000': 1,
            'increase|1-100': 1,
            'flag|1': [0, 1, -1]
          }
        ]
      }
    }
  }
}



