import $ from 'jquery'
import { fetchInterval } from '@/util/request'
import inNetworkHbs from '../templates/inNetwork.hbs'
import loader from '@/loader/base.config.js'
// 数字滚动
import DigitRoll from '@/util/digitroll.js'

export default class InNetwork {

  constructor() {
    $('.in-network').html(inNetworkHbs())
    this.interval = null
  }

  /**
   *  渲染各模块组件
   *  @return   {object}  null
   */
  render() {
    // 10分钟更新一次 请求全市在网终端、在网人数数据
    fetchInterval('fetchInNetwork', (data) => {
      this.digitRoll(data.inNetwork)
    }, loader.updateTime)
  }

  /**
   *  数字滚动
   *  @param    {object}  data 数据
   *  @return   {object}  null
   */
  digitRoll(data) {
    // 在网终端数据
    let terminal = data.terminal
    // 管控区域人数 数据
    let controlArea = data.controlArea  
    // 在网终端 container
    const roll1 = new DigitRoll({
      container:'.terminal .num-roll'
    })
    // 管控区域人数 container
    const roll2 = new DigitRoll({
      container:'.peopleNumer .num-roll'
    })
    // 在网终端
    roll1.roll(terminal)
    // 管控区域人数
    roll2.roll(controlArea)
    // 清除定时切换
    clearInterval(this.interval)
    // 定时切换
    this.interval = setInterval(function () {
      // 在网终端 (!=0 || null || undefined)
      if(terminal){
        terminal += parseInt(Math.random() * 10, 10)
        roll1.roll(terminal)
      }
      // 管控区域人数(!=0 || null || undefined)
      if(controlArea){
        controlArea += parseInt(Math.random() * 10, 10)
        roll2.roll(controlArea)
      }
      
    }, loader.aniTime) 
  }
}



