export default {
  url: '/main/search/:state/:type/:text',
  mock: {
    'code': 1,
    'msg': 'success',
    'result': {
      'searchList|0-10': [
        {
          'name': '@province',
          'value|1-3000': 1,
          'increase|1-100': 1,
          'flag|1': [0, 1, -1]
        }
      ]
    }
  }
}




