import $ from 'jquery'
import { fetch, fetchSocket } from '@/util/request'
import userHbs from '../templates/user.hbs'

export default class User {
  constructor() {
    $('.header-info').html(userHbs())
  }

  /**
   *  渲染
   *  @return   {object}  null
   */
  render() {
    const self = this
    // 获取用户名
    fetch('fetchUser', (data) => {
      $('.user-name').text(data)
    })
    // 调用渲染时间
    self.renderTime()
    // 调用退出系统
    self.exitSystem()
  }

  /**
   *  退出系统
   *  @return   {object}  null
   */
  exitSystem() {
    $('.out-login').on('click', () => {
      fetch('fetchLogout', (data) => {
        window.location.href = data
      })
    })
  }

  /**
   *  渲染时间
   *  @return   {object}  null
   */
  renderTime() {
    const self = this
    // 地本时间
    setInterval(() => {
      self.getCurrentDate()
    }, 1000)

    // 调用服务器时间
    // self.getServerDate()
  }

  /**
   *  获取服务器时间
   *  @return   {object}  null
   */
  getServerDate() {
    fetchSocket('fetchTimewebsocket', 1, (data) => {
      let date = JSON.parse(data.data) 
      date = date.result
      const year = date.slice(0, 4)
      const month = date.slice(4, 6)
      const day = date.slice(6, 8)
      const housr = date.slice(8, 10)
      const minutes = date.slice(10, 12)
      let time = `${year}年${month}月${day}日  ${housr}:${minutes}`
      $('.current-time').text(time)
    })
  }

  /**
   *  获取本地时间
   *  @return   {object}  null
   */
  getCurrentDate() {
    const self = this
    const date = new Date()
    const year = date.getFullYear()
    const month = self.formateDate(date.getMonth() + 1) 
    const day = date.getDate()
    const housr = self.formateDate(date.getHours())
    const minutes = self.formateDate(date.getMinutes())
    let time = `${year}年${month}月${day}日  ${housr}:${minutes}`
    $('.current-time').text(time)
  }
 
  /**
   *  格式化时间
   *  @param    {number}  t 时间字符串
   *  @return   {string}  时间格式
   */
  formateDate (t) {
    if (t < 10) {
      return `0${t}`
    }
    return t
  }
}



